import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { KycVerificationMethod, Store } from '../kyc/store';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-customer-onboarding',
  templateUrl: './customer-onboarding.page.html',
  styleUrls: ['./customer-onboarding.page.scss'],
})
export class CustomerOnboardingPage implements OnInit {
  cards: KycVerificationMethod[] = Store.onboardingMethod;

  constructor(private router: Router, public toastCtrl: ToastController) { }

  ngOnInit() {
  }

  goBack(){
    this.router.navigate(['dashboard'])
  }

  
  openLink(page: any) {
    if (page.link) {
      this.router.navigate([`${page.link}`]);
    } else {
      this.showToast();
    }
  }
  async showToast() {
    const toast = await this.toastCtrl.create({
      message: 'Service not Available',
      duration: 2000,
      position: 'bottom',
    });
    toast.present();
  }

}
